const writeData = () => {
    dataApi.map((data) => {
        return firebase.database().ref('data/').push(data);
    });
};
const readData = (category, data1) => {
    const bacaData = [];
    firebase.database().ref('data/').on('value', (snapshot) => {
        snapshot.forEach(childSnapshot => {
          const { judul, indikasi, hewan, ciri2, deskripsi, jenis, img, date } = childSnapshot.val();
          const temp1 = {
              id : childSnapshot.key,
              judul : judul,
              indikasi : indikasi,
              hewan : hewan,
              ciri2: ciri2,
              deskripsi : deskripsi,
              img : img,
              jenis : jenis,
              date : date,
          }
          // eslint-disable-next-line eqeqeq
          if(temp1.jenis.toLowerCase().indexOf(category)==!-1){
            bacaData.push(temp1);
          }
        })
    })
    return bacaData;
}

export { writeData, readData };