import NavbarCs from "../component/NavbarCs";
import { useParams } from "react-router-dom";
import React, { useEffect, useState } from "react";
import image from '../assets/image.png';
import { readData } from '../config/firebase/firebase'
import Form from "../component/Formx";
import Modal from "../component/Modal";

const Obat = () => {
    let {category} = useParams();
    const [dataApi] = useState(readData(category));
    const [dataCategory, setdataCategory] = useState([]);
    const inputHandle = (e) => {
        setdataCategory([]);
        let array = [];
        // eslint-disable-next-line eqeqeq
        if(e.detail.value==''){
          // eslint-disable-next-line array-callback-return
          dataApi.map((data) => {
            array.push(data);
          })
          setdataCategory(array);
        } else {
          // eslint-disable-next-line array-callback-return
          dataApi.map((data) => {
            // eslint-disable-next-line eqeqeq
            if(data.indikasi.toLowerCase().indexOf(e.detail.value) > -1  || data.judul.toLowerCase().indexOf(e.detail.value) > -1){
              array.push(data);
            }
          })
          setdataCategory(array);
          console.log(dataCategory);
        }
      }
    const [detail, setdetail] = useState({
        ciri2: '',
        date: '',
        deskripsi: '',
        hewan: '',
        id: '',
        img: '',
        indikasi: '',
        jenis: '',
        judul: '',
    });
    const [dataPost, setdataPost] = useState({
        judul : '',
        id: '',
        indikasi : '',
        hewan : '',
        ciri2: '',
        deskripsi : '',
        img : '',
    });
    const [imagex, setimagex] = useState(image);
    const inputForm = (e) => {
        const { id, value } = e.target;
        setdataPost(prevState => ({
                ...prevState,
                [id]: value
        }));
        console.log(id, '-', value)
    };
    const submit = () => {
        console.log(dataPost)
    };
    const toTitleCase = (str) => {
        return str.replace(
          /\w\S*/g,
          function(txt) {
            return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
          }
        );
    };
    const splite = (str, number) => {
        return str.split('',number).join('');
    };
    const setDetail = (e) => {
        setdetail(e);
    };
    const pickerImage = async (e) => {
        const FR= new FileReader();
        FR.onload = (temp) => {
            console.log(temp);
            setimagex(temp.target.result)
        }
        FR.readAsDataURL(e.target.files[0])
    };
    useEffect(()=> {
        inputHandle({detail : { value : '' }});
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);

    return (
        <>
            <NavbarCs />
            <div className="container pt-5">
                <div className="row">
                    <Form pickerImage={pickerImage} imagex={imagex} inputForm={inputForm} submit={submit}/>
                    <div className="col-lg-8 mb-3">
                        <div className="row mb-3">
                            <div className="col ms-3 me-3 rounded pt-2 bg-primary text-center">
                                <h3 className="text-light"><u>{toTitleCase(category)}</u></h3>
                            </div>
                        </div>
                        <div className="row">
                            {dataCategory.map((data)=>{
                                return (
                                    <div className="col-lg-6 mb-3" onClick={() =>{setDetail(data)}} data-bs-toggle="modal" data-bs-target="#exampleModal" key={data.id}>
                                        <div className="card">
                                            <div className="card-body">
                                                <div className="row">
                                                    <div className="col-5 col-md-4 col-lg-6">
                                                        <img src={data.img} className="card-img-top" alt="..." />
                                                    </div>
                                                    <div className="col-7 col-md-8 col-lg-6">
                                                        <h6>{data.judul}</h6>
                                                        <p className="card-text mb-1 fs-10">{splite(data.indikasi, 10)}</p>
                                                        <p className="card-text mb-1 fs-10">{data.hewan}</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                </div>
            </div>
            <Modal detail={detail} />
        </>
    )
  }

export default Obat;
